import { headers } from "next/headers";
import { whopsdk } from "@/lib/whop-sdk";
import { prisma } from "@/lib/prisma";
import { Card } from "@/components/ui/card";
import { formatCents } from "@/lib/utils";

interface PageProps {
  params: Promise<{ companyId: string }>;
}

export default async function CommunityDashboardPage({ params }: PageProps) {
  const { companyId } = await params;

  let userId: string;
  try {
    const headersList = await headers();
    const result = await whopsdk.verifyUserToken(headersList);
    userId = result.userId;
  } catch {
    return (
      <div className="min-h-screen bg-[var(--waliet-bg)] flex items-center justify-center p-6">
        <Card className="max-w-md text-center">
          <h1 className="text-xl font-semibold text-[var(--waliet-text)] mb-2">
            Access Required
          </h1>
          <p className="text-[var(--waliet-text-secondary)]">
            Please access this page through your Whop dashboard.
          </p>
        </Card>
      </div>
    );
  }

  // Check admin access
  const access = await whopsdk.users.checkAccess(companyId, { id: userId });
  if (access.access_level !== "admin") {
    return (
      <div className="min-h-screen bg-[var(--waliet-bg)] flex items-center justify-center p-6">
        <Card className="max-w-md text-center">
          <h1 className="text-xl font-semibold text-[var(--waliet-text)] mb-2">
            Admin Access Required
          </h1>
          <p className="text-[var(--waliet-text-secondary)]">
            You need admin access to view this dashboard.
          </p>
        </Card>
      </div>
    );
  }

  // Get or create community config
  let config = await prisma.communityConfig.findUnique({
    where: { whopCompanyId: companyId },
  });

  if (!config) {
    config = await prisma.communityConfig.create({
      data: { whopCompanyId: companyId },
    });
  }

  // Get stats
  const thirtyDaysAgo = new Date(Date.now() - 30 * 24 * 60 * 60 * 1000);

  const [totalSellers, recentSessions, recentVolume] = await Promise.all([
    prisma.sellerProfile.count({ where: { isActive: true } }),
    prisma.session.count({
      where: {
        status: "COMPLETED",
        endedAt: { gte: thirtyDaysAgo },
      },
    }),
    prisma.purchase.aggregate({
      where: {
        status: "COMPLETED",
        communityId: companyId,
        createdAt: { gte: thirtyDaysAgo },
      },
      _sum: { totalAmount: true },
    }),
  ]);

  const totalVolume = recentVolume._sum.totalAmount || 0;
  const communityRevenue = Math.round(
    totalVolume * (config.communityFeeBps / 10000)
  );

  return (
    <div className="min-h-screen bg-[var(--waliet-bg)] p-6">
      <div className="max-w-6xl mx-auto">
        <h1 className="text-2xl font-semibold text-[var(--waliet-text)] mb-6">
          Waliet Dashboard
        </h1>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
          <Card>
            <p className="text-[var(--waliet-text-secondary)] text-sm">
              Your Share
            </p>
            <p className="text-3xl font-semibold text-[var(--waliet-accent)] mt-1">
              {formatCents(communityRevenue)}
            </p>
            <p className="text-[var(--waliet-text-tertiary)] text-sm mt-1">
              This month
            </p>
          </Card>

          <Card>
            <p className="text-[var(--waliet-text-secondary)] text-sm">
              Total Volume
            </p>
            <p className="text-3xl font-semibold text-[var(--waliet-text)] mt-1">
              {formatCents(totalVolume)}
            </p>
            <p className="text-[var(--waliet-text-tertiary)] text-sm mt-1">
              This month
            </p>
          </Card>

          <Card>
            <p className="text-[var(--waliet-text-secondary)] text-sm">
              Active Sellers
            </p>
            <p className="text-3xl font-semibold text-[var(--waliet-text)] mt-1">
              {totalSellers}
            </p>
            <p className="text-[var(--waliet-text-tertiary)] text-sm mt-1">
              Listing time
            </p>
          </Card>

          <Card>
            <p className="text-[var(--waliet-text-secondary)] text-sm">
              Sessions
            </p>
            <p className="text-3xl font-semibold text-[var(--waliet-text)] mt-1">
              {recentSessions}
            </p>
            <p className="text-[var(--waliet-text-tertiary)] text-sm mt-1">
              This month
            </p>
          </Card>
        </div>

        {/* Configuration */}
        <Card>
          <h2 className="font-semibold text-[var(--waliet-text)] mb-4">
            Configuration
          </h2>
          <div className="space-y-4">
            <div className="flex items-center justify-between py-3 border-b border-[var(--waliet-border)]">
              <div>
                <p className="text-[var(--waliet-text)]">Revenue Share</p>
                <p className="text-sm text-[var(--waliet-text-secondary)]">
                  Your cut of each transaction
                </p>
              </div>
              <span className="text-xl font-semibold text-[var(--waliet-text)]">
                {config.communityFeeBps / 100}%
              </span>
            </div>

            <div className="flex items-center justify-between py-3 border-b border-[var(--waliet-border)]">
              <div>
                <p className="text-[var(--waliet-text)]">Platform Fee</p>
                <p className="text-sm text-[var(--waliet-text-secondary)]">
                  Waliet&apos;s fee on each transaction
                </p>
              </div>
              <span className="text-xl font-semibold text-[var(--waliet-text)]">
                8%
              </span>
            </div>

            <div className="flex items-center justify-between py-3">
              <div>
                <p className="text-[var(--waliet-text)]">Require Approval</p>
                <p className="text-sm text-[var(--waliet-text-secondary)]">
                  New sellers need your approval
                </p>
              </div>
              <span className="text-xl font-semibold text-[var(--waliet-text)]">
                {config.requireApproval ? "Yes" : "No"}
              </span>
            </div>
          </div>
        </Card>
      </div>
    </div>
  );
}
